.portfolio-item {
    border: 1px solid transparent;
    transition: border 0.3s ease-in-out;
}

.portfolio-item:hover {
    border: 1px solid #6b6b6b;
}

.item-wrap {
    position: relative;
}

.item-wrap a {
    color: white;
}

.image-wrap {
    width: auto;
    height: 10rem;
}

.image-wrap img {
    width: 100%;
    object-fit: cover;
    height: 10rem;
}

.github {
    background-color: whitesmoke;
    display: flex;
    justify-content: center;
}

.web {
    background-color: white;
    display: flex;
    justify-content: center;
}

.github:hover,
.web:hover {
    background-color: #8b8b8b;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: url(../../images/overlay-bg.png) repeat;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}

.portfolio-item:hover .overlay {
    opacity: 1;
    -moz-opacity: 1;
    filter: alpha(opacity=100);
}

@media (max-width: 675px) {
    .image-wrap {
        width: 15rem;
        height: 10rem;
        background-color: red;
    }
    .overlay {
        opacity: 0.9;
    }
}