header {
    position: relative;
    height: 100vh;
    min-height: 500px;
    width: 100%;
    background: #161415;
    text-align: center;
    overflow: hidden;
}

header:before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}

header .banner {
    display: inline-block;
    vertical-align: middle;
    margin: 0 auto;
    width: 85%;
    padding-bottom: 30px;
    text-align: center;
}

header .banner-text {
    width: 100%;
}

header .banner-text h1 {
    font-size: 3.5rem;
    color: #fff;
    margin: 0 auto 18px auto;
}

header .banner-text h3 {
    font-size: 1.2rem;
    color: #a8a8a8;
    margin: 0 auto;
    width: 70%;
}

header .banner-text h3 span,
header .banner-text h3 a {
    color: #fff;
}

header .banner-text hr {
    width: 60%;
    margin: 18px auto 24px auto;
    border-color: #2f2d2e;
    border-color: rgba(150, 150, 150, 0.1);
}

header .social {
    margin: 24px 0;
    padding: 0;
    font-size: 30px;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}

header .social li {
    display: inline-block;
    margin: 0 15px;
    padding: 0;
}

header .social li a {
    color: #fff;
}

header .social li a:hover {
    color: #11abb0;
}

#nav-wrap ul,
#nav-wrap li,
#nav-wrap a {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

#nav-wrap {
    font-size: 0.7rem;
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    margin: 0 auto;
    z-index: 100;
    position: fixed;
    left: 0;
    top: 0;
}

ul#nav {
    min-height: 48px;
    width: auto;
    text-align: center;
    background-color: #161415;
}

ul#nav li {
    position: relative;
    list-style: none;
    height: 48px;
    display: inline-block;
}

ul#nav li a {
    display: inline-block;
    padding: 8px 13px;
    line-height: 32px;
    text-decoration: none;
    text-align: left;
    color: #fff;
    transition: color 0.2s ease-in-out;
}

ul#nav li a:hover {
    color: #11abb0;
}

.hamburger {
    position: absolute;
    top: 10px;
    right: 20px;
    display: none !important;
}

@media (max-width: 860px) {
    .hamburger {
        display: block !important;
        z-index: 100;
    }
    #nav.nav {
        background-color: rgba(57, 189, 178, 0);
        display: flex;
        flex-direction: column;
        font-size: 1.5rem;
        position: absolute;
        width: 100%;
        left: -100%;
        transition: all 0.3s ease;
    }
    #nav.nav li {
        padding: 2.5rem 0;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    #nav.nav.active {
        left: 0;
        background-color: rgba(57, 189, 178, 0.9);
    }
    ul#nav li a:hover {
        color: white;
    }
    #nav.nav li:hover {
        background-color: rgba(57, 189, 178, 1);
    }
}