#footer {
    background-color: #161415;
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.social>* {
    padding: 1rem;
    color: rgba(255, 255, 255, 0.3);
}

.social>*:hover {
    color: rgba(255, 255, 255, 1);
}

#footer h3 {
    padding: 2rem 0;
    color: white;
    font-size: 0.8rem;
    font-weight: 200;
}

.footer-uparrow {
    position: absolute;
    top: -40px;
}