#portfolio {
    height: auto;
    padding: 3rem 0;
    background-color: #2b2b2b;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#portfolio h1 {
    text-align: center;
    color: white;
    padding-bottom: 2rem;
}

.portfolio-wrap {
    width: 1200px;
    max-width: 90%;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
}

@media (max-width: 900px) {
    .portfolio-wrap {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 675px) {
    .portfolio-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}