#about {
    height: 100vh;
    background: whitesmoke;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-wrap {
    width: 1200px;
    max-width: 80%;
    padding-top: 4rem;
    display: flex;
    justify-content: space-around;
}

.profile-img {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
}

.profile-img img {
    max-width: 100%;
    height: auto;
    transition: all 5 ease;
}

.profile-img img:hover {
    transform: scale(1.1);
}

.about-text {
    text-align: left;
    width: 600px;
    max-width: 80%;
}

.about-text h2 {
    text-align: center;
}

.about-text .contact-details {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
}

.aboutme p {
    line-height: 1.5rem;
    color: #424242;
}

.aboutme h2 {
    padding-bottom: 1rem;
}

.contact-info h3 {
    padding-bottom: 0.4rem;
}

.contact-info p {
    padding-bottom: 0.2rem;
}

.resume {
    padding: 0.8rem;
    width: 40%;
    align-self: flex-start;
    border: none;
    cursor: pointer;
    border: 1px solid #11abb0;
    border-radius: 0.2rem;
    font-weight: 600;
    font-size: 0.9rem;
    background-color: white;
    transition: all 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.resume:hover {
    background-color: #11abb0;
    color: white;
    transform: scale(1.1);
}

.resume a {
    text-decoration: none;
    color: black;
    margin-left: 1rem;
}

@media (max-width: 1100px) {
    .about-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 5rem;
    }
}

@media (max-width: 750px) {
    .contact-details button {
        margin-top: 3rem;
        font-size: 1.2rem;
        width: 100%;
    }
    #about {
        height: auto;
    }
    .about-text {
        font-size: 1.1rem;
        text-align: left;
        margin-top: 1.2rem;
    }
    .contact-details {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .contact-info h3 {
        text-align: center;
        font-size: 2rem;
    }
    .aboutme h2 {
        font-size: 2rem;
    }
}