@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300&display=swap);

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: "Kanit", sans-serif;
    scroll-behavior: smooth;
}

p {
    line-height: 1.1rem;
}
#about {
    height: 100vh;
    background: whitesmoke;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-wrap {
    width: 1200px;
    max-width: 80%;
    padding-top: 4rem;
    display: flex;
    justify-content: space-around;
}

.profile-img {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
}

.profile-img img {
    max-width: 100%;
    height: auto;
    transition: all 5 ease;
}

.profile-img img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.about-text {
    text-align: left;
    width: 600px;
    max-width: 80%;
}

.about-text h2 {
    text-align: center;
}

.about-text .contact-details {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
}

.aboutme p {
    line-height: 1.5rem;
    color: #424242;
}

.aboutme h2 {
    padding-bottom: 1rem;
}

.contact-info h3 {
    padding-bottom: 0.4rem;
}

.contact-info p {
    padding-bottom: 0.2rem;
}

.resume {
    padding: 0.8rem;
    width: 40%;
    align-self: flex-start;
    border: none;
    cursor: pointer;
    border: 1px solid #11abb0;
    border-radius: 0.2rem;
    font-weight: 600;
    font-size: 0.9rem;
    background-color: white;
    transition: all 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.resume:hover {
    background-color: #11abb0;
    color: white;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.resume a {
    text-decoration: none;
    color: black;
    margin-left: 1rem;
}

@media (max-width: 1100px) {
    .about-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 5rem;
    }
}

@media (max-width: 750px) {
    .contact-details button {
        margin-top: 3rem;
        font-size: 1.2rem;
        width: 100%;
    }
    #about {
        height: auto;
    }
    .about-text {
        font-size: 1.1rem;
        text-align: left;
        margin-top: 1.2rem;
    }
    .contact-details {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .contact-info h3 {
        text-align: center;
        font-size: 2rem;
    }
    .aboutme h2 {
        font-size: 2rem;
    }
}
#footer {
    background-color: #161415;
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.social>* {
    padding: 1rem;
    color: rgba(255, 255, 255, 0.3);
}

.social>*:hover {
    color: rgba(255, 255, 255, 1);
}

#footer h3 {
    padding: 2rem 0;
    color: white;
    font-size: 0.8rem;
    font-weight: 200;
}

.footer-uparrow {
    position: absolute;
    top: -40px;
}
header {
    position: relative;
    height: 100vh;
    min-height: 500px;
    width: 100%;
    background: #161415;
    text-align: center;
    overflow: hidden;
}

header:before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}

header .banner {
    display: inline-block;
    vertical-align: middle;
    margin: 0 auto;
    width: 85%;
    padding-bottom: 30px;
    text-align: center;
}

header .banner-text {
    width: 100%;
}

header .banner-text h1 {
    font-size: 3.5rem;
    color: #fff;
    margin: 0 auto 18px auto;
}

header .banner-text h3 {
    font-size: 1.2rem;
    color: #a8a8a8;
    margin: 0 auto;
    width: 70%;
}

header .banner-text h3 span,
header .banner-text h3 a {
    color: #fff;
}

header .banner-text hr {
    width: 60%;
    margin: 18px auto 24px auto;
    border-color: #2f2d2e;
    border-color: rgba(150, 150, 150, 0.1);
}

header .social {
    margin: 24px 0;
    padding: 0;
    font-size: 30px;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}

header .social li {
    display: inline-block;
    margin: 0 15px;
    padding: 0;
}

header .social li a {
    color: #fff;
}

header .social li a:hover {
    color: #11abb0;
}

#nav-wrap ul,
#nav-wrap li,
#nav-wrap a {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

#nav-wrap {
    font-size: 0.7rem;
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    margin: 0 auto;
    z-index: 100;
    position: fixed;
    left: 0;
    top: 0;
}

ul#nav {
    min-height: 48px;
    width: auto;
    text-align: center;
    background-color: #161415;
}

ul#nav li {
    position: relative;
    list-style: none;
    height: 48px;
    display: inline-block;
}

ul#nav li a {
    display: inline-block;
    padding: 8px 13px;
    line-height: 32px;
    text-decoration: none;
    text-align: left;
    color: #fff;
    transition: color 0.2s ease-in-out;
}

ul#nav li a:hover {
    color: #11abb0;
}

.hamburger {
    position: absolute;
    top: 10px;
    right: 20px;
    display: none !important;
}

@media (max-width: 860px) {
    .hamburger {
        display: block !important;
        z-index: 100;
    }
    #nav.nav {
        background-color: rgba(57, 189, 178, 0);
        display: flex;
        flex-direction: column;
        font-size: 1.5rem;
        position: absolute;
        width: 100%;
        left: -100%;
        transition: all 0.3s ease;
    }
    #nav.nav li {
        padding: 2.5rem 0;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    #nav.nav.active {
        left: 0;
        background-color: rgba(57, 189, 178, 0.9);
    }
    ul#nav li a:hover {
        color: white;
    }
    #nav.nav li:hover {
        background-color: rgba(57, 189, 178, 1);
    }
}
#portfolio {
    height: auto;
    padding: 3rem 0;
    background-color: #2b2b2b;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#portfolio h1 {
    text-align: center;
    color: white;
    padding-bottom: 2rem;
}

.portfolio-wrap {
    width: 1200px;
    max-width: 90%;
    display: grid;
    grid-gap: 2rem;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
}

@media (max-width: 900px) {
    .portfolio-wrap {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 675px) {
    .portfolio-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.portfolio-item {
    border: 1px solid transparent;
    transition: border 0.3s ease-in-out;
}

.portfolio-item:hover {
    border: 1px solid #6b6b6b;
}

.item-wrap {
    position: relative;
}

.item-wrap a {
    color: white;
}

.image-wrap {
    width: auto;
    height: 10rem;
}

.image-wrap img {
    width: 100%;
    object-fit: cover;
    height: 10rem;
}

.github {
    background-color: whitesmoke;
    display: flex;
    justify-content: center;
}

.web {
    background-color: white;
    display: flex;
    justify-content: center;
}

.github:hover,
.web:hover {
    background-color: #8b8b8b;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: url(data:image/png;base64,) repeat;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}

.portfolio-item:hover .overlay {
    opacity: 1;
    -moz-opacity: 1;
    filter: alpha(opacity=100);
}

@media (max-width: 675px) {
    .image-wrap {
        width: 15rem;
        height: 10rem;
        background-color: red;
    }
    .overlay {
        opacity: 0.9;
    }
}
